import { ColumnDef, Row } from '@tanstack/react-table';
import user from '@/assets/imgs/user.png';
import ViewIcon from '@/components/icons/ViewIcon';
import PrinterIcon from '@/components/icons/PrinterIcon';
import { Contract } from '../../types/contract.types';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<Contract> }) => {
    return (
        <div className="flex justify-center gap-6">
            <ViewIcon className="cursor-pointer text-primery-blue-400" />
            <PrinterIcon className="cursor-pointer text-primery-blue-400" />
        </div>
    );
};

export const columns: ColumnDef<Contract>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[100%]">
                {row.original.employee?.name}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رقم الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[100%]">
                {row.original.employee?.employee_number}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    القسم
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original?.employee?.department}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الحالة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-xs text-white-blue-50 font-medium tracking-normal leading-[115%]">
                    <p className="border bg-[#22AD5C] p-1 w-fit rounded-sm">{row.original?.status}</p>
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الأجراءات
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
